import React, { Component } from "react";
import {
  Tab,
  Tabs,
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button,
  CardMenu,
  IconButton
} from "react-mdl";
import { Link } from "react-router-dom";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }
  toggleCategories() {
    if (this.state.activeTab === 0) {
      return (
        <div className="projects-grid">
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#fff",
                height: "156px",
                background:
                  "url(https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRlavGtkAIwZS210J5aJsj5qglYcbRD_wviT-fi7xecO-NTw4qe) center / cover"
              }}
            ></CardTitle>

            <CardText>SpringbootRestAPIReactJSMicroservice</CardText>
            <CardActions boarder>
              <Link to="https://github.com/shilpahemaraj/SpringbootRestAPIReactJSMicroservice" />
              <Button colored>Github</Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    } else if (this.state.activeTab === 1) {
      return (
        <div>
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#fff",
                height: "170px",
                background:
                  " url (data:image/png;base64,) center / cover "
              }}
            ></CardTitle>
            <CardText>amplifyreactgraphqlblogapp</CardText>
            <CardActions boarder>
              <Button colored>Github</Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    } else if (this.state.activeTab === 2) {
      return (
        <div>
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#fff",
                height: "170px",
                background:
                  " url (https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQ3qpBF5isq2WIwj-E9sOp7oChrvJ5FhJTLsrvG71JQ2YyWCHyB) center / cover "
              }}
            ></CardTitle>
            <CardText>myportfolio-reactjs</CardText>
            <CardActions boarder>
              <Button colored>Github</Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    }
  }

  render() {
    return (
      <div className="category-tabs">
        <Tabs
          activeTab={this.state.activeTab}
          onChange={tabId => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>Springboot</Tab>
          <Tab>Graphql</Tab>
          <Tab>React</Tab>
        </Tabs>

        <Grid>
          <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Projects;



